import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {MemeComponent} from './memes/meme/meme.component';
import { MemelistComponent } from './memes/memelist/memelist.component';
import { MemesComponent } from './memes/memes.component';



const routes: Routes = [

  
  {path:'' , redirectTo:'meme/list', pathMatch:'full'},
  {path :'meme', component: MemesComponent ,
  children: [
    {path:'upload',component :MemeComponent},
    {path:'list', component: MemelistComponent},
    
    
    
  ]
   } ,
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  

})
export class AppRoutingModule { }
